import React, { useState } from 'react'
import { Link, useHistory } from 'react-router-dom';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import useClasses from './style';
import appleImg from './icons/apple.png';
import facebookImg from './icons/facebook.png';
import googleImg from './icons/google.png';
import checkIcon from './icons/check.png';
import logo from './icons/logo.png';
import {
    Box,
    Paper,
    Button,
    Typography,
    InputLabel,
  } from '@material-ui/core'
import auth from 'API-Service/auth';
import { useToasts } from 'react-toast-notifications';
import { GoogleLogin } from 'react-google-login';
import AppleLogin from 'react-apple-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';

function SignUp() {
  const classes = useClasses();
  const { addToast } = useToasts();
  const [showError, setShowError] = useState(true);
  const [eightError, setEightError] = useState(true);
  const [capitalError, setCapitalError] = useState(true);
  const [numberError, setNumberError] = useState(true);
  const [confirmPassError, setConfirmPassError] = useState(false);
  const [email, setEmail] = useState('');
  const [pass, setPass] = useState('');
  const [confirmPass, setConfirmPass] = useState('');
  const [eyeVisible, setEyeVisible] = useState(false);
  const [confirmEyeVisible, setConfirmEyeVisible] = useState(false);
  const [isSocial, setIsSocial] = useState(false);
  const [step, setStep] = useState(0);
  const history = useHistory()

  function validateEmail(mail) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
      setShowError(false)
    } else {
      setShowError(true)
    }
  }

  const handleEmail = (e) => {
    validateEmail(e.target.value);
    setEmail(e.target.value);
  }

  const passwordValidation = (pass) => {
    setEightError(pass.length < 8);
    setNumberError(!/\d/.test(pass));
    setCapitalError(!/[A-Z]/.test(pass));
  }
  const handlePasswordChange = (e) => {
    passwordValidation(e.target.value)
    setPass(e.target.value)
  }

  const handleConfirmPasswordChange = (e) => {
    if (pass === e.target.value) {
      setConfirmPassError(false)
      setConfirmPass(e.target.value);
    } else {
      setConfirmPassError(true);
      setConfirmPass('');
    }
  }

  const handleEye = () => {
    setEyeVisible(!eyeVisible)
  }

  const handleConfirmEye = () => {
    setConfirmEyeVisible(!confirmEyeVisible)
  };

  const handleSignup = (e) => {
    e.preventDefault();
    if (email && pass && confirmPass) {
      auth
      .register(email, pass, isSocial)
      .then(response => {
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){setStep(1)}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      })
    };
  }

  const handleGoogleLogin = (response) => {
    addToast("Successfully signed up with google", { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
    setTimeout(function(){history.push('/login');}, 1000);
  }

  const handleGoogleFail = (e) => {
    console.log(e)
    addToast("Google Login Failed", { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
  }

  const handleFacebookLogin = (response) => {
    addToast("Successfully signed up with facebook", { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
    setTimeout(function(){history.push('/login');}, 1000);
  }

  const handleFacebookFailure = (e) => {
    console.log(e)
    addToast("Facebook Sign Up Failed", { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
  }

  const handleAppleLogin = (response) => {
    console.log(response)
    addToast("Successfully signed up with apple", { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
    setTimeout(function(){history.push('/login');}, 1000);
  }

  return (
    <div className={classes.root}>
      {
        step == 0 ?
        <>
          <Paper className={classes.loginFormCard} style={{margin: "0px"}}>
            <Typography className={classes.title}> Sign Up </Typography>
            <form noValidate autoComplete="off"
              style={{margin: "2px"}}
            >
              <Box className={email.length != 0 && showError && email.length != 0 ? classes.errorBox : email.length == 0 ? classes.inputGroup : classes.successBox}>
                <InputLabel className={classes.inputLabel}> Email</InputLabel>
                <input 
                  type="email"
                  id=""
                  defaultValue=""
                  placeholder="Enter your email.."
                  helperText=""
                  onChange={(e) => handleEmail(e)}
                  className={classes.input}
                  value={email}
                />
                { 
                  (email.length != 0 && showError) && <Typography className={classes.error}> Email is not correct </Typography>
                }
              </Box>
              <Box className={(capitalError || numberError || eightError) ? classes.errorBox : classes.inputGroup}>
                <InputLabel className={classes.inputLabel}>
                  Password 
                </InputLabel>
                <Box className={classes.passInputGroup}
                  style={{border: `1px solid ${(capitalError || numberError || eightError) ? "#E2E2E8" : "#108F65"}`}}
                >
                  <input 
                    type={eyeVisible ? "text" : "password"}
                    id=""
                    defaultValue=""
                    placeholder="Enter your password.."
                    helperText=""
                    onChange={(e) => handlePasswordChange(e)}
                    value={pass}
                  />
                  <VisibilityOutlinedIcon 
                    className={classes.eye} 
                    onClick={() => handleEye()}
                    style={{color: `${eyeVisible ? "#6871EA" : ""}`}}
                  />
                </Box>
                <Box className={eightError ? classes.password_error : classes.password_confirm}>
                  At least 8 characters
                </Box>
                <Box className={capitalError ? classes.password_error : classes.password_confirm}>
                  1 capitalError
                </Box>
                <Box className={numberError ? classes.password_error : classes.password_confirm}>
                  1 number
                </Box>
              </Box>

              <Box className={confirmPassError ? classes.errorBox : classes.inputGroup}>
                <InputLabel className={classes.inputLabel}>
                  Confirm password
                </InputLabel>
                <Box className={classes.passInputGroup}
                  style={{border: `1px solid ${(!confirmPassError && confirmPass)? "#108F65" : confirmPassError ? "#E04369" : "#E2E2E8"}`}}
                >
                  <input 
                    type={confirmEyeVisible ? "text" : "password"}
                    id=""
                    defaultValue=""
                    placeholder="Repeat your password.."
                    helperText=""
                    onChange={(e) => handleConfirmPasswordChange(e)}
                  />
                  <VisibilityOutlinedIcon 
                    className={classes.eye} 
                    onClick={() => handleConfirmEye()}
                    style={{color: `${confirmEyeVisible ? "#6871EA" : ""}`}}
                  />
                </Box>
                {
                  confirmPassError && <Typography className={classes.error}
                    style={{marginTop: ""}}
                  > Passwords do not match </Typography>
                }
              </Box>

              <Box>
                <Button fullWidth
                  className={classes.loginBtn}
                  style={{
                    textTransform: "capitalize"
                  }}
                  onClick={(e)=> handleSignup(e)}
                  disabled={showError || eightError || numberError || capitalError || !confirmPass}
                >
                  Sign up
                </Button>
                
              </Box>
            </form>
            <Box className={classes.dividerBlock}>
                <Typography> Or </Typography>
            </Box> 
            <Box className={classes.socialBtnGrp}>
              <GoogleLogin
                clientId={process.env.REACT_APP_GOOGLE_KEY}
                onSuccess={handleGoogleLogin}
                onFailure={handleGoogleFail}
                buttonText="Google"
                cookiePolicy={'single_host_origin'}
                autoLoad={false}
                render={renderProps => (									
                  <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                    <Box
                      component="img"
                      alt="Google"
                      src={googleImg}
                    />
                  </Button>
                )}
              />
              <FacebookLogin
                appId={process.env.REACT_APP_FACEBOOK_KEY}
                autoLoad={false}
                callback={handleFacebookLogin}
                onFailure={handleFacebookFailure}
                render={renderProps => (																		
                  <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                    <Box
                      component="img"
                      alt="Facebook"
                      src={facebookImg}
                    />
                  </Button>
                )}
              />
              <AppleLogin 
                clientId={'test-app.clutchwallet.xyz'}
                redirectURI="https://test-app.clutchwallet.xyz/login"
                callback={handleAppleLogin}												
                autoLoad={false}		
                render={renderProps => (																		
                  <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                    <Box
                      component="img"
                      alt="Apple"
                      src={appleImg}
                    />
                  </Button>
                )}
              />
            </Box>
          </Paper>
          <Box>
            <Typography className={classes.title1}
              style={{marginTop: "2rem", display: 'flex'}}
            >
              Already have an account?
              <Link to='/login' className={classes.link}> Log In </Link>
            </Typography>
          </Box>
        </>
        :
        <Box className={classes.card}>
          <Box 
            component="img"
            alt="logo"
            src={logo}
          />
          <Typography className={classes.headline}>
            Great! Now verify your email
          </Typography>
          <Typography className={classes.paragraph1}>
            Please check your mailbox to verify you email <b>{email}</b> The verification link will expire in one hour
          </Typography>
        </Box>
      }
    </div>
  )
}

export default SignUp